* {
  font-family: 'Orbitron', sans-serif;
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  scroll-behavior: smooth;
}
/* 100vh for sectons */
body {
  background-color: #EEEEEE;
  /* background-image: url("./images/background.svg"); */
  background-repeat: no-repeat;
  background-attachment: fixed;
}
.up-button{
  border-radius: 50%;
  background-color: #F05454;
  width: 2em;
  height: 2em;
  position: fixed;
  bottom: 60px;
  right: 50px;
  text-decoration: none;
  text-align: center;
  color: #0a0a0e;
}
.up-button img{
width: 2em;
}
.cv {
  position: absolute;
  z-index: 2;
  top: 20px;
  right: 15px;
  font-size: 1.2em;
}
.cv a {
  color: #7cdedc;
  text-decoration: none;
}
.cv a:hover {
  color: #e9e3e6;
}
.logo a{
  width: fit-content;
}
.navbar img {
  margin-left: 8rem;
  margin-top: 1.5rem;
  max-width: 10%;
  padding-top: 0.7rem;
  padding-bottom: 0.5rem;

}
#navicon{
  display: none;
}
#navicon2{
  display: none;
}

.navbar {
  background: rgba(0, 0,0, 0.00001);
  width: 100%;
  height: 100%;
  display: flex;
  display: inline-flex;
  flex-wrap: wrap;
  margin-top: 0.5rem;
  
}
.navigation{
  display: flex;
  align-items: center;
  justify-content: right;
  margin-left: auto;
  margin-right: 1.3rem;
  width: 40%;
  

  
}

.navigation a {
  width: fit-content;
  color: #121212;
  text-decoration: none;
  font-weight: 500;
  font-size: 1.2em;
  padding-left: 1.25rem;
  padding-right: 1.25rem;
  height: 50px;
  text-align: center;
  line-height: 50px;

}
.navigation a:hover {
  background-color: #d1d1d1 ;

}
/* home section */
.home {
  padding: 10px 20px;
  width: 100%;
  min-height: 90vh;
  display: flex;
  align-items: center;
}
.home h1 {
  color: #121212;
  font-size: 1.5em;
  font-weight: 500;
}
.home h1 span {
  display: inline-block;
  margin-top: 10px;
  font-size: 2em;
  font-weight: 600;
  color: #30475E;
}
.home h2 {
  color: #121212;
  font-size: 1.8em;
  font-weight: 700;
  letter-spacing: 1px;
  margin-top: 10px;
  margin-bottom: 30px;
}
.description {
  width: fit-content;
  margin-left: 7rem;
  margin-bottom: 8rem
}

.personalimg {
  margin-left: auto;
  margin-right: 6rem;
  margin-bottom: 7rem;
  width: 25%;
}
.btn {
  color: #EEEEEE;
  background-color: #F05454;
  font-size: 1.1em;
  font-weight: 600;
  display: inline-block;
  padding: 0.9375em 2.1875em;
  letter-spacing: 1px;
  border-radius: 15px;
  margin: 30px auto;
  border-width: 0px;
  transition: 0.7s ease;
  text-decoration: none;
}
.btn:hover {
  transform: scale(1.1);
}

.social-accounts a {
  color: #30475E;
  font-size: 2.7em;
  padding-right: 10px;
  padding-left: 10px;
}
.fa-brands {
  transition: 0.7s ease;
}

.fa-linkedin:hover {
  color: #0a66c2;
  transform: scale(1.1);
}
.fa-github:hover {
  color: #24292f;
  transform: scale(1.1);
}
/* About section */
.About{
  display: flex;
  min-height: 80vh;
}
.About p{
  max-width: 40%;
  margin-top: 100px;
  margin-left: 150px;
  margin-right: 150px;
color: #121212;
font-size: 1.2em;
font-weight: 500;
line-height: 40px;
}
.About img{
  width: 30%;
  height: 30%;
  margin-left: 200px;
  margin-top: 50px;
}
.btn2 {
  color: #EEEEEE;
  background-color: #F05454;
  font-size:0.85em;
  font-weight: 600;
  display: inline-block;
  padding: 0.9375em 2.1875em;
  letter-spacing: 1px;
  border-radius: 15px;
  margin: 30px auto;
  border-width: 0px;
  transition: 0.7s ease;
  text-decoration: none;
  width: 14.2rem;
  height: 4.3rem;
}
.btn2:hover {
  transform: scale(1.1);
}
.cards {
  width: 100%;
  min-height: 80vh;
  align-items: center;
  /* background-image: url("./images/background1.svg"); */
  padding-top: 8em;
}
.title {
  display: flex;
  justify-content: center;
  color: #30475E;
  font-size: 2.2em;
  font-weight: 800;
  padding-top: 30px;
}
.content {
  display: flex;
  flex-direction: row;
  justify-content: center;
  flex-wrap: wrap;
}
.card {
  background-color: #F05454;
  width: 21.25em;
  box-shadow: 0 5px 25px rgba(247, 244, 244, 0.2);
  padding: 25px;
  margin: 45px;
  transition: 0.7s ease;
  border-radius: 10%;
}
.card:hover {
  transform: scale(1.1);
}
.icon {
  color: #0a0a0e;
  font-size: 6em;
  text-align: center;
}
.icon img {
  width: 1.5em;
}
.info {
  text-align: center;
}
.info h2 {
  font-size: 1.2em;
  font-weight: 700;
  margin: 10px;
}
.skills {
  /* background-image: url("./images/background1.svg"); */
  width: 100%;
  min-height: 70vh;
  align-items: center;
  padding-bottom: 150px;
}
.skills-icons,
.skills-icons2 {
  display: flex;
  justify-content: center;
  padding-top: 50px;
}
.skills-icons img,
.skills-icons2 img {
  height: 9.375em;
  margin: 20px 50px;
}
.skills-icons2 {
  margin-left: 20px;
}
figure {
  text-align: center;
}
figcaption {
  color: #121212;
  font-size: 1.1em;
  font-weight: 600;
}
.projects {
  /* background-image: url("./images/background1.svg"); */
  padding-bottom: 10px;
  max-height: 100vh;
  width: 100%;
}

.project-image1 {
  text-align: center;
  width: 100%;
  height: 100%;
  z-index: 2;
  position: relative;
  opacity: 0;
  margin-top: 2.5rem;
}
.project-image1 img {
  width: 15em;
  padding: 10px;
}
.project-image2 {
  position: relative;
  top: -450px;
  z-index: 1;
  text-align: center;
  opacity: 0;
}
.project-image2 img {
  width: 50%;
}
.right-arrow {
  width: 5em;
  height: 5em;
  border-radius: 100%;
  border-color: #000000;
  position: relative;
  float: right;
  top: 300px;
  right: 250px;
  z-index: 3;
}

.left-arrow {
  width: 10%;
  height: 10%;
  width: 5em;
  height: 5em;
  border-radius: 50%;
  border-color: #000000;
  position: relative;
  float: left;
  left: 250px;
  top: 300px;
  z-index: 3;
  padding-top: 10px;
}
.left-arrow img {
  transform: rotate(180deg);
}
.left-arrow img,
.right-arrow img {
  width: 3em;
}
.active {
  opacity: 1;
  transition: 1s ease;
}
.project-title {
  color: #121212;
  margin-top: 30px;
}

#K1 {
  width: 17em;
}

footer {
  /* background-color: #0a0a0e; */
  color: #e9e3e6;
  padding: 1.5em;
  display: flex;
  justify-content: space-between;
}
footer p {
  font-size: 1em;
  font-weight: 600;
  color: #121212;
}
footer p a {
  color: #121212;

  text-decoration: none;
}
footer p span {
  color: #30475E;
}
footer .social-accounts a {
  font-size: 1.6em;
}
@media (max-width: 1023px) {
  
  .up-button{
    width: 5em;
    height: 5em;
  }
  .up-button img{
    width: 100%;
  }

  .navigation {
    display: flex;
  }
  #navigation a {
    font-size: 1.5em;
  }
  
  .navbar img {
    max-width: 35%;
    margin-left:2rem ;
  }
  .navigation a{
    display: none;
  }
  #navicon {
    display: block;
    width: 25%;
    height: 50%;
    margin-left: auto;
    margin-top: 20px;
  }
  #navicon:hover {
  background-color: #EEEEEE;
  color: #30475E;
  }

  #navicon i {
  font-size: 2em;
  line-height: 55px;
  }
  .responsive {
    position: fixed;
    top: 0;

    height: 100%;
    width:100%;
    display:flex;
    align-items: center;
    justify-content: center;
    gap: 1.5rem;
    background-color: #121212;
    flex-direction: column;
    z-index: 10;
    
  }

  .responsive #navicon {
    display: none;
  }
  .responsive #navicon2 i {
      font-size: 2em;
  }
  .responsive #navicon2 {
    display: inline-block;
    position: absolute;
    top:4rem;
    right:4rem;
  }
  .responsive a{
    float: none;
    display: block;
    text-align: left;
    color: #EEEEEE;
    text-decoration: none;
    font-size: 2rem  !important;
    line-height: 7em;
    
  }
  
    .title {
    font-size: 4.2em;
  }
  .home {
    margin-top: 300px;
    padding: 80px 20px;
    flex-direction: column;
    align-items: center;
    min-height: 100vh;
    margin-left: -100px;
  }
  .description{
    margin-top: -250px;
  }
  .home h1 {
    font-size: 4em;
  }
  .home h2 {
    font-size: 2.5em;
  }
  .home h1 span {
    font-size: 1.8em;
  }
  .home img {
    margin-left: 100px;
    width: 100%;
  }
  .personalimg{
    max-width: 80%;
    margin-right: 1rem;
  }
  .btn{
    
    width: 50%;
    height: 50%;
    font-size: 2.3em;

  }
  .social-accounts a {
    
    font-size: 4.7em;
    
  }
  .About{    
  flex-direction: column ;
}
.About img{
  width:80%;
}
.About p{
  font-size: 3em;
  max-width: 100%;
  line-height: 70px;
  margin-left: 100px;
  margin-right: 50px;

}
.btn2{
  width: 14em;
  height: 4em;
  font-size: 0.7em;
}
  .skills {
    margin-top: 180px;
    min-height: 80vh;
    margin-bottom: -100px;
  }
  .skills-icons{
    margin-top: 50px;
  }
  .skills-icons img,
  .skills-icons2 img {
    height: 15em;
    margin-top: 50px;
  }
  figcaption{
    font-size: 2.5em;
  }
  #projects {
    max-height: 70vh;
  }
  .right-arrow {
    right: 10px;
  }
  .left-arrow {
    left: 10px;
  }
  .project-image1 {
    margin-top: 100px;
  }
  .project-image2 {
    margin-top: -200px;
  }
  .project-image1 img {
    width: 10em;
  }
  .project-image2 img {
    width: 80%;
  }
  .secondryimg{
    display: none;
  }
  #K1 {
    width: 27em;
  }
  .cards {
  }
  .card {
    width: 40em;
    height: 40em;
  }
  .icon img {
    width: 50%;
  }
  .info {
    font-size: 2.5em;
  }

  .content {
    flex-direction: column;
    align-items: center;
  }
  footer .social-accounts a {
    font-size: 3.5em;
  }
  #footerp {
    width: 100%;
    font-size: 1.5em !important;
  }
}
